import {Component, DoCheck, ElementRef, Input, OnChanges, OnInit, ViewChild} from '@angular/core';
import {HttpClient} from '@angular/common/http';
declare const $: any;
declare const appjustablekitUIkit: any;

export interface SlideItem {
  bgColor: string;
  bgImage?: string;
}

export interface SlideshowWidgetSettings {
  componentPrefix: string;
  slides: SlideItem[];
  animation: string;
  autoplay: boolean;
  paralax: boolean;
  finite: boolean;
  autoplayInterval: number;
  minHeight: number;
  viewportHeight: number;
  dotnav: boolean;
  theme: string;
}

@Component({
  selector: 'app-slideshow-generator',
  templateUrl: './slideshow-generator.component.html',
  styleUrls: ['./slideshow-generator.component.scss']
})
export class SlideshowGeneratorComponent implements OnInit, DoCheck, OnChanges  {
  @Input() input: any;

  public generatedHtml = '';
  public hideContentId = true;
  public customStyle = '';

  public widget: SlideshowWidgetSettings = {
    componentPrefix: Math.floor(Math.random() * 1000000).toString(),
    slides: [{bgColor: '#eeeeee'}, {bgColor: '#e5eeee'}],
    animation: 'slide',
    autoplay: true,
    paralax: false,
    finite: false,
    autoplayInterval: 3000,
    viewportHeight: 50,
    minHeight: 400,
    dotnav: true,
    theme: 'light'
  };

  public drag = {
    start: null,
    end: null
  };

  public imageUploadError = '';
  public imageUploading = false;


  @ViewChild('generatedElement') generatedElement: ElementRef;
  @ViewChild('generatedCSS') generatedCSS: ElementRef;

  constructor(private http: HttpClient) {
    window.addEventListener('message',  (evt) => {
      console.log('message', evt);
      if (evt.data.action === 'settings:load') {
        this.initWidgetState(evt.data.data.settings);
      }
    });
  }

  ngOnChanges() {
    if (this.input) {
      this.initWidgetState(this.input);
    }
  }

  ngDoCheck() {
    setTimeout(() => {
      if (this.customStyle !== this.generatedCSS.nativeElement.innerText) {
        this.customStyle = this.generatedCSS.nativeElement.innerText;
        $('#generatedStyle').remove();
        $(`<style id="generatedStyle">${this.customStyle}</style>`)
          .appendTo(this.generatedElement.nativeElement);
      }
    });
  }

  ngOnInit() {
    console.log('input', this.input);
  }

  private initWidgetState(data: any): void {
    if (!data.templateData) {
      return;
    }

    const templateData: SlideshowWidgetSettings = JSON.parse(data.templateData);
    if (templateData.componentPrefix && templateData.slides) {
      this.widget.slides =  templateData.slides;
      this.widget.componentPrefix =  templateData.componentPrefix;
    }
  }

  public removeSlide(slide: SlideItem): void {
    if (this.widget.slides.length === 1) {
      return;
    }
    const index = this.widget.slides.indexOf(slide);
    this.widget.slides.splice(index, 1);
  }

  public addSlide(): any {
    this.widget.slides.push({
      bgColor: '#eeeeee'
    });
  }

  public generateHtml(): void {
    console.log(this.generatedElement);
    this.generatedHtml = this.generatedElement.nativeElement.innerHTML;
  }

  public saveHtml(): void {
    this.hideContentId = false;

    setTimeout(() => {
      const libs = `
        <div style="display: none" data-load-css="https://appjustable.com/weebly/artem/libs/css/uikit.css"></div>
        <div style="display: none" data-load-js="https://appjustable.com/weebly/artem/libs/js/uikit.js"></div>`;

      this.generatedHtml = this.generatedElement.nativeElement.innerHTML;
      this.generatedHtml = this.generatedHtml.replace(/<!--([\s|\S]*?)-->/g, '');
      this.generatedHtml = this.generatedHtml.replace(/_ngcontent([\s|\S]*?)""/g, '');
      this.generatedHtml = this.generatedHtml.replace(/></g, '>\n<');
      this.generatedHtml = this.generatedHtml.replace(/  /g, ' ');

      parent.postMessage({
        action: 'settings:update',
        data: {
          markup: libs + this.generatedHtml,
          markup_editor: libs + this.generatedHtml,
          templateData: JSON.stringify(<SlideshowWidgetSettings> {
            slides: this.widget.slides,
            componentPrefix: this.widget.componentPrefix
          }),
          elId: 'ajSlideshow'
        }
      }, (<any>window).frameOrigin);
      console.log(this.generatedElement.nativeElement.innerHTML);
      this.hideContentId = true;
    });
  }

  onFileChanged(event, slide) {
    const file = event.target.files[0];
    const uploadData = new FormData();
    this.imageUploading = true;
    this.imageUploadError = '';
    uploadData.append('fileToUpload', file, file.name);

    this.http.post('https://appjustable.com/weebly/artem/upload/index.php', uploadData, {
      reportProgress: true,
      observe: 'events'
    }).subscribe((res: any) => {
      console.log(res); // handle event here
      if (res.type === 4) {
        this.imageUploading = false;
        if (res && res.body && res.body.res) {
          slide.bgImage = 'https://appjustable.com/weebly/artem/upload/images/' + res.body.res;
          console.log('slide', slide);
        } else {
          this.imageUploadError = 'Something went wrong, try to upload another image';
        }
      }
    });
  }

  getSliderOptions(): string {
    let res = '';
    res += `animation: ${this.widget.animation}; `;
    res += `autoplay: ${this.widget.autoplay}; `;
    res += `autoplay-interval: ${this.widget.autoplayInterval}; `;
    if (this.widget.minHeight) {
      res += `min-height: ${this.widget.minHeight}; `;
    }

    return res;
  }

  goToSlide(index: number): void {
    appjustablekitUIkit.slideshow('#slideshow-' + this.widget.componentPrefix).show(index);
  }


  onSliderDragStart(i: number) {
    this.drag.start = i;
  }

  onSliderDragOver(i: number, event) {
    const temp = this.widget.slides[this.drag.start];

    this.widget.slides.splice(this.drag.start, 1);
    this.widget.slides.splice(i, 0, temp);
    this.drag.start = i;

    event.dataTransfer.dropEffect = 'move';  // See the section on the DataTransfer object.

    return false;
  }

  onSliderDragEnd() {
    event.preventDefault();
  }
}
