import {Component, DoCheck, ElementRef, Input, OnChanges, OnInit, ViewChild} from '@angular/core';
declare const $: any;

export interface FilterItem {
  filterId: string;
}

export interface FilterGroup {
  id: string;
  name: string;
  bgColor: string;
  activeColor: string;
  borderColor: string;
  boxShadow: boolean;
  disabled?: boolean;
  isActive?: boolean;
}

export interface FilterWidgetSettings {
  filters: FilterGroup[];
  items: FilterItem[];
  componentPrefix: string;
  masonry: boolean;
}


@Component({
  selector: 'app-filter-generator',
  templateUrl: './filter-generator.component.html',
  styleUrls: ['./filter-generator.component.scss']
})
export class FilterGeneratorComponent implements OnInit, DoCheck, OnChanges  {
  @Input() input: any;
  public isWidgetVisible = true;

  public newGroupName = 'New Filter';
  public generatedHtml = '';
  public componentPrefix = Math.floor(Math.random() * 1000000).toString();
  public hideContentId = true;
  public customStyle = '';

  public masonry = false;

  public drag = {
    type: '', // filter or item
    start: null,
    end: null
  };

  public filters: FilterGroup[] = [{
    name: 'All',
    id: 'all',
    bgColor: '#F3F9FF',
    borderColor: '#F3F9FF',
    activeColor: '#00137e',
    boxShadow: true,
    disabled: true,
    isActive: true
  }, {
    name: 'BLUE',
    id: '1',
    bgColor: '#F3F9FF',
    borderColor: '#F3F9FF',
    activeColor: '#00137e',
    boxShadow: true
  }, {
    name: 'GRAY',
    id: '2',
    bgColor: '#EEEEEE',
    borderColor: '#EEEEEE',
    activeColor: '#000000',
    boxShadow: true
  }, {
    name: 'RED',
    id: '3',
    bgColor: '#881414',
    borderColor: '#881414',
    activeColor: '#ae0d00',
    boxShadow: true
  }];

  public items: FilterItem[] = [{
    filterId: '1',
  }, {
    filterId: '1',
  }, {
    filterId: '2',
  }, {
    filterId: '2',
  }, {
    filterId: '3',
  }, {
    filterId: '3',
  }];

  @ViewChild('generatedElement') generatedElement: ElementRef;
  @ViewChild('generatedCSS') generatedCSS: ElementRef;

  constructor() {}

  ngOnChanges() {
    if (this.input) {
      this.initWidgetState(this.input);
    }
  }

  toggleMasonry(): void {
    this.isWidgetVisible = false;
    setTimeout(() => {
      this.isWidgetVisible = true;
    });
  }

  setActive(filter: FilterGroup) {
    this.filters.forEach((item: FilterGroup) => {
      item.isActive = false;
      this.toggleMasonry();
    });
    filter.isActive = true;
  }

  trackById(item: any): any {
    return item.id;
  }

  getFilterOptions(): string {
    let res = '';
    res += `masonry: ${this.masonry}; `;

    return res;
  }

  ngDoCheck() {
    if (this.generatedElement && this.customStyle !== this.generatedCSS.nativeElement.innerText) {
      this.customStyle = this.generatedCSS.nativeElement.innerText;
      $('#generatedStyle').remove();
      $(`<style id="generatedStyle">${this.customStyle}</style>`)
        .appendTo(this.generatedElement.nativeElement);
    }
  }

  ngOnInit() {
    console.log('input 2', this.input);
  }

  private initWidgetState(data: any): void {
    if (!data.templateData) {
      return;
    }

    const templateData: FilterWidgetSettings = JSON.parse(data.templateData);
    if (templateData.componentPrefix && templateData.filters) {
      console.log('set data');
      this.filters =  templateData.filters;
      this.items =  templateData.items;
      this.masonry =  templateData.masonry;
      this.componentPrefix =  templateData.componentPrefix;
    }
  }

  public removeItem(array: any[], item: any): void {
    const index = array.indexOf(item);
    array.splice(index, 1);
  }

  public addGroup(): any {
    this.filters.push({
      id: '' + Math.floor(Math.random() * 10000),
      name: this.newGroupName,
      bgColor: '#EEEEEE',
      borderColor: '#EEEEEE',
      activeColor: '#000000',
      boxShadow: true
    });
  }

  public getFilterById(id: string): FilterGroup {
    return this.filters.find((item) => {
      return item.id === id;
    });
  }

  public addItem(filter: FilterGroup): any {
    this.items.push({ filterId: filter.id});
  }

  public generateHtml(): void {
    console.log(this.generatedElement);
    this.generatedHtml = this.generatedElement.nativeElement.innerHTML;
  }

  public saveHtml(): void {
    this.hideContentId = false;

    setTimeout(() => {
      const libs = `
        <div style="display: none" data-load-css="https://appjustable.com/weebly/artem/libs/css/uikit.css"></div>
        <div style="display: none" data-load-js="https://appjustable.com/weebly/artem/libs/js/uikit.js"></div>`;

      this.generatedHtml = this.generatedElement.nativeElement.innerHTML;
      this.generatedHtml = this.generatedHtml.replace(/<!--([\s|\S]*?)-->/g, '');
      this.generatedHtml = this.generatedHtml.replace(/_ngcontent([\s|\S]*?)""/g, '');
      this.generatedHtml = this.generatedHtml.replace(/></g, '>\n<');
      this.generatedHtml = this.generatedHtml.replace(/  /g, ' ');

      parent.postMessage({
        action: 'settings:update',
        data: {
          markup: libs + this.generatedHtml,
          markup_editor: libs + this.generatedHtml,
          templateData: JSON.stringify(<FilterWidgetSettings> {
            filters: this.filters,
            items: this.items,
            masonry: this.masonry,
            componentPrefix: this.componentPrefix
          }),
          elId: 'ajFilter'
        }
      }, (<any>window).frameOrigin);
      console.log(this.generatedElement.nativeElement.innerHTML);
      this.hideContentId = true;
    });
  }

  onFilterDragStart(i: number, type: string) {
    this.drag.type = type;
    this.drag.start = i;
  }

  onFilterDragOver(i: number, array, type, event) {
    if (type === this.drag.type) {
      const temp = array[this.drag.start];
      array.splice(this.drag.start, 1);
      array.splice(i, 0, temp);
      this.drag.start = i;

      event.dataTransfer.dropEffect = 'move';  // See the section on the DataTransfer object.

      return false;
    }
  }

  onFilterDragEnd(event) {
    this.drag.type = null;
    event.preventDefault();
  }
}
