import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  activeComponent = 'filter';
  // activeComponent = 'slideshow';
  widgetInput: any;

  constructor() {
    if ((<any>window).initWidget) {
      this.activeComponent = (<any>window).initWidget;
    }

    if ((<any>window).widgetInput) {
      this.widgetInput = (<any>window).widgetInput;
    }

    window.addEventListener('message',  (evt) => {
      console.log('message', evt);
      if (evt.data.action === 'settings:load') {
        this.widgetInput = evt.data.data.settings;
        this.showWidget(this.widgetInput);
      }
    });
  }

  public showWidget(data: any) {
    const templateData: any = JSON.parse(data.templateData);
    if (templateData.slides) {
      this.activeComponent = 'slideshow';
    } else if (templateData.filters) {
      this.activeComponent = 'filter';
    }
  }
}
